import { Typography, Button } from '@material-ui/core'
import React from 'react'
import { HomeCard } from './Styles'
import { Grid } from '@material-ui/core'

export default function Home() {
    return (
        <Grid container direction='column' justifyContent='center' alignItems='center'>


            <Grid item xs={12}>
                <HomeCard>
                    <Typography variant='h2' style={{ textAlign: 'center', color: 'white', fontFamily: 'Montserrat', fontWeight: '700' }}>Efficient Solutions for a Sustainable Future</Typography>
                    <Typography variant='h5' style={{ textAlign: 'center', color: 'white', fontFamily: 'Montserrat', fontWeight: '500', margin: '1rem' }}>
                        We provide EPC services in Solar, Electrical, lightning Protection and Process Cooling
                    </Typography>

                    <Button variant='contained' size='large' style={{ backgroundColor: '#DE403D', color: 'white' }}>Learn More</Button>
                </HomeCard>

            </Grid>


        </Grid>
    )
}
